<template>
  <view class="order-detail">
    <orderDetail :order="order" :isShowBase="!isAccept" @reload="handleGetInfo"></orderDetail>
    <!-- 选择人员 -->
    <view class="checkUnitBox greenBox" v-if="isAccept">
      <view class="searchInput">
        <input
          :placeholder="`搜索${this.mode === 'plaform' ? '组织' : '人才'}`"
          v-model="unitKeyword"
          @confirm="searchUnit(unitKeyword)"
          placeholder-style="color:#A8ABB2;font-size:14px"
        />
        <image
          src="https://cdn-static.papa.com.cn/jingAn/icon/black-search.svg"
          class="searchIcon"
          @click="searchUnit(unitKeyword)"
          mode=""
        />
      </view>
      <view class="staffList">
        <view class="staffItem" v-for="item in unitList" :key="item.id" @click="changeSelect(item)">
          <view class="left">
            <view class="name">{{ item.name }}</view>
            <view v-if="this.mode !== 'plaform'">{{ item.phone }}</view>
          </view>
          <view v-if="item.checked" class="checkIcon">
            <uv-icon name="checkbox-mark" size="16"></uv-icon>
          </view>
        </view>
      </view>
    </view>
    <!-- 订单状态区块 -->
    <view class="btmBtn">
      <template v-if="showOprBtn">
        <bottomButton buttonType="plain" @click.stop="refuseOrder" style="flex: 1">
          拒单
        </bottomButton>
        <bottomButton @click.stop="changeAcceptStauts" style="flex: 1">接单</bottomButton>
      </template>
      <template v-if="order.serveWorkDevStatus === 'serve_wait' && showStyle">
        <bottomButton @click.stop="handlePublishStyle(order)" style="flex: 1">
          发布服务风采
        </bottomButton>
      </template>
      <template v-if="isAccept">
        <bottomButton buttonType="plain" @click.stop="changeAcceptStauts" style="flex: 1">
          取消
        </bottomButton>
        <bottomButton @click.stop="handleAccept" style="flex: 1">确定</bottomButton>
      </template>
    </view>
    <rejectPopue
      ref="rejectPopue"
      @confirm="handleReject"
      :editable="true"
      :reason="order.rejectReason"
    />
  </view>
</template>

<script>
import serviceDetailTitle from '../components/serviceDetailTitle.vue'
import orderDetail from '../components/orderDetail.vue'
import { getOrderInfo } from '@/apis/sportsService/serviceOrder'
import { getPlatformOrderPage, getOrgOrderPage } from '@/apis/sportsService/common'
import rejectPopue from './components/rejectPopue.vue'
import { getOrderList, orgDispatch, platformDispatch } from '@/apis/sportsService/serviceAssign.js'
import themeMixins from '@/mixins/themeMixins.js'
import { timeDifference } from '@/utils/timeUtil'
import bottomButton from '@/components/bottomButton.vue'
export default {
  mixins: [themeMixins],
  components: {
    serviceDetailTitle,
    orderDetail,
    rejectPopue,
    bottomButton,
  },
  data() {
    return {
      order: {},
      status: 'wait', // wait:待派，service:待服务，comment:待评价，reject:已拒单，finish:已完成
      formList: [
        {
          label: '派单时间',
          value: '2025/10/18 12:00:00',
        },
        {
          label: '接单组织',
          value: '靖安县广场舞协会',
        },
        {
          label: '服务时间',
          value: '2025/10/18 12:00:00',
        },
        {
          label: '时间',
          value: '南门社区广场',
        },
        {
          label: '原因',
          value: '需求不明确，目前没有匹配人员和资源，请重新描述',
        },
        {
          label: '联系人',
          value: '芒果',
        },
        {
          label: '手机号码',
          value: '17417513326',
        },
      ],
      staffList: [],
      isAccept: false, // 是否接单状态
      unitList: [],
      organStaffList: [],
      selectedList: [],
      mode: 'plaform', // org/plaform // 分组织和平台
      orgId: '', // 组织id
      serviceId: '', // 服务id
      submited: false,
      showStyle: '',
    }
  },
  computed: {
    showOprBtn() {
      if (this.isAccept) return false
      // 判断mode
      if (this.mode === 'org') {
        return this.order?.serveWorkDevStatus === 'org_wait'
      } else {
        // 平台
        return (
          this.order?.serveWorkDevStatus === 'platform_wait' ||
          this.order?.serveWorkDevStatus === 'org_wait'
        )
      }
    },
  },
  onLoad(options) {
    this.setNavigationBarColor()
    const { id, mode, orgId, showStyle } = options
    console.log({ id, mode })
    this.mode = mode
    this.orgId = orgId
    this.serviceId = id
    this.showStyle = showStyle
  },
  onShow() {
    this.handleGetInfo()
    this.submited = false
  },
  mounted() {
    // uni.setNavigationBarTitle({ title: '已完成' })
    this.getAssginUnit()
  },
  methods: {
    // 获取详情
    async handleGetInfo() {
      const res = await getOrderInfo(this.serviceId)
      if (res.code === 0) {
        console.log({ res })
        this.order = res.data
        uni.setNavigationBarTitle({ title: res.data.serveWorkStatStatusCn })
        //   this.formatFormList(res.data.serveWorkStatStatus)
      } else {
        uni.showToast({ title: res.msg, icon: 'none' })
      }
    },
    // 前往服务风采发布页面
    handlePublishStyle(item) {
      // 算出serveStartDate- serveEndDate差了多少个小时
      const servicTimeGap = timeDifference(
        new Date(item.serveStartDate).getTime(),
        new Date(item.serveEndDate).getTime(),
      )
      console.log({ servicTimeGap, item })

      uni.navigateTo({
        url: `/pagesSub/sportsService/serviceStyle/form?id=${item.serveWorkId}&timeGap=${servicTimeGap.toFixed(1)}&peopleNum=${item.peopleNum}`,
      })
    },
    // 切换接单模式
    changeAcceptStauts() {
      this.isAccept = !this.isAccept
      if (!this.isAccept) {
        // 清空选中
        this.selectedList = []
      }
    },
    // 勾选人员\组织
    changeSelect(e) {
      console.log({ e, mode: this.mode })
      if (this.mode == 'plaform') {
        // 选择组织
        // 寻找目标组织
        const index = this.unitList.findIndex((item) => item.id === e.id)

        if (e.checked) {
          //  取消选中并剔除在selectedList中的数据
          this.unitList[index].checked = false
          this.selectedList = []
        } else {
          // 全部取消选中
          this.unitList.forEach((item) => {
            item.checked = false
          })
          // 选中并添加到selectedList中
          this.unitList[index].checked = true
          this.selectedList = [e]
        }
      } else {
        // 更新选中状态
        // 寻找目标人才
        const index = this.unitList.findIndex((item) => item.id === e.id)
        if (e.checked) {
          //  取消选中并剔除在selectedList中的数据
          this.unitList[index].checked = false
          this.selectedList = this.selectedList.filter((item) => item.id !== e.id)
        } else {
          // 选中并添加到selectedList中
          this.unitList[index].checked = true
          this.selectedList.push(e)
        }
      }
    },
    // 接单
    async handleAccept(item) {
      console.log({ item, mode: this.mode })
      // 判断是否选择了组织
      if (this.selectedList.length === 0) {
        uni.showToast({
          title: `请选择${this.mode == 'org' ? '组织' : '人员'}`,
          icon: 'none',
        })
        return
      }
      if (this.submited) return
      this.submited = true
      if (this.mode == 'org') {
        // 组织接单
        await this.handleOrgAccept(item)
      } else {
        // 平台接单
        await this.handlePlatformAccept(item)
      }
      // 弹窗提示
      uni.showToast({
        title: '接单成功',
        icon: 'success',
      })
      this.handleGetInfo()
      this.isAccept = false
    },
    // 组织接单
    async handleOrgAccept(item) {
      const res = await orgDispatch({
        processType: 'platform',
        serveWorkId: this.order.serveWorkId,
        sportTalentIds: this.selectedList.map((item) => item.id).join(','),
      })
      if (res.code === 0) {
        return 'ok'
      }
    },
    // 平台接单
    async handlePlatformAccept(item) {
      const res = await platformDispatch({
        processType: 'platform',
        serveWorkId: this.order.serveWorkId,
        organizationId: this.selectedList[0].id,
      })
      if (res.code === 0) {
        // 弹窗提示
        return 'ok'
      }
    },
    // 拒单
    async handleReject(value) {
      console.log({ value })

      if (this.mode == 'org') {
        // 组织接单
        await this.handleOrgReject(value)
      } else {
        // 平台接单
        await this.handlePlatformReject(value)
      }
      uni.showToast({
        title: '拒单成功',
        icon: 'success',
      })
      this.$refs.rejectPopue.close()
      this.handleGetInfo()
    },
    // 平台拒单
    async handlePlatformReject(value) {
      const res = await platformDispatch({
        processType: 'reject',
        serveWorkId: this.order.serveWorkId,
        rejectReason: value,
      })
      if (res.code === 0) {
        return 'ok'
      }
    },
    // 组织拒单
    async handleOrgReject(value) {
      const res = await orgDispatch({
        processType: 'reject',
        serveWorkId: this.order.serveWorkId,
        rejectReason: value,
      })
      if (res.code === 0) {
        return 'ok'
      }
    },
    // 搜索组织
    searchUnit(name) {
      this.getAssginUnit(name)
    },
    // 获取组织列表
    async getAssginUnit(name) {
      // 判断类型
      if (this.mode == 'org') {
        this.handleGetOrganStaffList(this.orgId, name)
      } else {
        this.handleGetOrganList(name)
      }
    },
    // 获取组织列表
    async handleGetOrganList(keyword = '') {
      const res = await getPlatformOrderPage({
        page: 1,
        size: 999,
        type_id: '8',
        keyword,
      })
      if (res.code === 0) {
        console.log({ res })

        this.unitList = res.data.listData.map((item) => ({
          ...item,
          checked: false,
        }))
      }
    },
    // 获取组织人员列表
    async handleGetOrganStaffList(organization_id, keyword = '') {
      const res = await getOrgOrderPage({
        page: 1,
        size: 999,
        organization_id,
        keyword,
      })
      if (res.code === 0) {
        console.log({ people: res })
        this.unitList = res.data.listData.map((item) => ({
          ...item,
          // 若只有一个则默认选中
          checked: false,
        }))
        //
        if (this.unitList.length === 1) {
          this.changeSelect(this.unitList[0])
        }
      }
    },
    // 拒绝订单
    refuseOrder() {
      this.$refs.rejectPopue.open()
    },
    viewStyle() {
      uni.navigateTo({ url: '/pagesSub/sportsService/serviceStyle/detail?id=1' })
    },
    commentOrder() {
      uni.navigateTo({ url: '/pagesSub/sportsService/serviceComment/index?orderId=1' })
    },
    retryOrder() {
      uni.navigateTo({ url: '/pagesSub/sportsService/sprotOrder/form?orderId=1' })
    },
    // 前往评价页面
    toEvaluePage() {
      uni.navigateTo({
        url: `/pagesSub/sportsService/serviceStyle/detail?id=${this.order.serveMienId}`,
      })
    },
    // 重新提交
    reSubmit() {},
    // 发布风采
    publishStyle() {},
    // 接单
    acceptOrder() {},
    // 查看评价
    checkEvaluePage() {},
  },
}
</script>

