    }

    .searchIcon {
      width: 56rpx;
      height: 56rpx;
    }
  }

  .staffList {
    margin-top: 24rpx;
    max-height: 60vh;
    overflow-y: auto;

    .staffItem {
      padding: 24rpx 8rpx;
      height: 100rpx;
      display: flex;
      justify-content: space-between;
      align-items: center;
